:root {
    --text-input-font-default: #494b50;
    --global-background-default: #fefbf4;
    --btn-font-default: #ffffff;
    --btn-font-hover: #ffffff;
    --btn-background-default: #20232a;
    --btn-background-hover: #a2958e;
    --btn-border-default: #20232a;
    --btn-border-hover: #a2958e;
}

html {
    font-size: 62.5%;
    color: var(--text-input-font-default);
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }  

body {
    --header-height: 6rem;
    font-size: 1.6rem;
    font-family: Arial, Helvetica, sans-serif;
    font-style: normal;
    text-align: center;
    background-color: var(--global-background-default);
}

.sofi-heading {
    font-size: 4.5rem;
    line-height: 1.222;
}

.sofi-title {
    font-size: 2.4rem;
    line-height: 1.333;
}

.sofi-subtitle {
    font-size: 2.1rem;
    line-height: 1.357;
}

.sofi-text {
    font-size: 1.6rem;
    line-height: 1.625;
}

.sofi-btn {
    font-size: 1.4rem;
    line-height: 1.429;
    padding: 0.7rem 1.6rem;
    display: inline-flex;
    justify-content: center;
    border-radius: 9999px;
    cursor: pointer;
    color: var(--btn-font-default);
    background-color: var(--btn-background-default);
    border: 1px solid var(--btn-border-default);
}

.sofi-btn:hover {
    color: var(--btn-font-hover);
    background-color: var(--btn-background-hover);
    border-color: var(--btn-border-hover);
}

.sofi-media {
    width: 50px;
    margin-bottom: 10px;
}

.s3 {
    margin-top: 1.2rem;
}

.s5 {
    margin-top: 1.6rem;
}

.s7 {
    margin-top: 2.4rem;
}